    font-size: 14px;
    color: #4f5c65;
    margin: 0; }
  :global(.miconsent__option.accept_all) :global(button) {
    color: #ffc823; }

:global(.miconsent__scriptlist) :global(>) :global(div) {
  padding: 5px;
