    border-top-right-radius: 5px; }
  :global(.miconsent__scriptlist) :global(>) :global(div:last-of-type) {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom: 0.5px solid #eee; }

:global(.miconsent__scriptlist_item) {
  display: flex;
  align-items: center; }
  :global(.miconsent__scriptlist_item_icon) {
    width: 20px;
