    min-width: 20px;
    min-height: 20px;
    margin-right: 10px; }
  :global(.miconsent__scriptlist_item) :global(span) {
    font-size: 14px; }
  :global(.miconsent__scriptlist_item) :global(small) {
    font-size: 12px;
    color: #4f5c65;
    line-height: 14px;
    display: block; }
  :global(.miconsent__scriptlist_item) :global(>) :global(div) {
    padding-right: 10px; }

:global(.miconsent__accept_all) {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px; }
