<main>
  {#if !closed}
    <div class="miconsent {blocking ? 'blocking' : ''}">
      <div class="miconsent__container">
        {#if viewState === 'HOME'}
          <Home
            {...homeText}
            {highlightColor}
            {sliderColor}
            {scripts}
            on:done={handleGrantDone}
            on:close={handleClose}
            on:changeView={changeView} />
        {:else if viewState === 'CHOOSE'}
          <Choose
            {...chooseText}
            {highlightColor}
            {sliderColor}
            {scripts}
            on:done={handleDone}
            on:changeView={changeView} />
        {/if}
      </div>
    </div>
  {/if}
</main>
